.correction-banner-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 60px;
  position: absolute;
  width: 100%;
  bottom: 0;
}

.show-banner {
  background: #FFFFFF;
}

.correction-banner {
  width: 100%;
}

.correction-message {
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 600;
  text-align: center;
  line-height: 60px;
}

.correction-message img {
  display: inline;
  height: 15px;
  vertical-align: baseline;
  margin-left: 20px;
}

.correct {
  background-color: #0086381a;
}

.correct .correction-message {
  color: #008638;
}

.almost-correct {
  background-color: #fc630f1a;
}

.almost-correct .correction-message {
  color: #fc630f;
}

.incorrect {
  background-color: #d300181a;
}

.incorrect .correction-message {
  color: #d30018;
}

.show_answer {
  background-color: #007A9C1A;
}

.show_answer .correction-message {
  color: #007A9C;
}

