.App *,
.App ::before,
.App ::after {
  box-sizing: border-box;
}

.crossword_container {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}

.label {}

.description_wrapper {
  display: flex;
  flex-direction: row;;
  align-items: flex-start;
  text-align: center;
}

.description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: left;
  flex-grow: 1;
}

.description>p,
.description>ol {
  margin-top: 0;
}

.input_fields {
  width: 350px;
}

.input_fields h5 {
  padding: 0;
  margin: 0;
}

.zoom_area_container {
  display: flex;
  align-self: center;
  overflow: hidden;
  width: 100%;
  justify-content: center;
  align-items: center;
  height: 564px;
}

.control-container {
  display: flex;
  z-index: 9999999;
  width: 100%;
  position: absolute;
  bottom: -80px;
}

.control-container button {
  display: block;
  border: 0.12em solid var(--border-medium, #919191);
}

.table_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  margin: 0 1em;
  overflow: hidden;
  background: var(--surface-page, rgba(255, 255, 255, 1));
}

.crossword {
  border-collapse: collapse;
  width: 450px;
  overflow: auto;
  margin: 1px;
}

.crossword .td {
  position: relative;
  padding: 0;
  font-size: large;
  font-weight: 500;
  user-select: none;
  touch-action: none;
  box-sizing: border-box;
}

.crossword .td::before {
  display: block;
  content: "";
  padding-top: 100%;
}

.crossword .filled {
  border: 0.052em solid var(--border-medium, #919191);
}

.crossword .empty {
  visibility: hidden;
}

.hidden {
  visibility: hidden;
}

.generateButton {
  width: max-content;
}

@media screen and (max-width: 480px) {
  .zoom_area_container {
    padding: 0 10px;
    height: 100%;
  }

  .crossword {
    width: 370px;
  }
}

.crossword-list-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 1em;
}

.words_listing {
  width: 50%;
  max-width: 350px;
}

.words_listing>ul {
  position: relative;
}

.words_listing>ul>li {
  display: flex;
  justify-content: space-between;
  border-bottom: 0.1em solid var(--border-medium, #919191);
  padding-right: 3em;
}

.words_listing>ul>li>button {
  position: absolute;
  right: 0;
  border: none;
}

.error_message {
  color: red;
}

@media screen and (max-width: 600px) {
  .description_wrapper {
    flex-direction: column;
    align-items: center;
  }

  .crossword-list-container {
    flex-direction: column;
    padding: 0;
  }

  .words_listing {
    width: 100%;
    max-width: unset;
  }

  .words_listing>ul {
    margin: 1em;
    padding: 0;
  }
}

.buttons_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 1em;
}

button {
  cursor: pointer;
}