.App *,
.App ::before,
.App ::after {
  box-sizing: border-box;
  border: none;
}

.App ::before,
.App ::after {
  --tw-content: "";
}

.App,
.App :host {
  line-height: 1.5;
  -webkit-text-size-adjust: 100%;
  -moz-tab-size: 4;
  tab-size: 4;
  font-family: "Open Sans";
}

.App body {
  margin: 0;
  line-height: inherit;
}

.App hr {
  height: 0;
  color: inherit;
  border-top-width: 1px;
}

.App abbr:where([title]) {
  text-decoration: underline dotted;
}

.App h1,
.App h2,
.App h3,
.App h4,
.App h5,
.App h6 {
  font-size: inherit;
  font-weight: inherit;
}

.App a {
  color: inherit;
  text-decoration: inherit;
}

.App b,
.App strong {
  font-weight: bolder;
}

.App code,
.App kbd,
.App samp,
.App pre {
  font-family: "Open Sans";
  font-size: 1em;
}

.App small {
  font-size: 80%;
}

.App sub,
.App sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

.App sub {
  bottom: -0.25em;
}

.App sup {
  top: -0.5em;
}

.App table {
  text-indent: 0;
  border-color: inherit;
  border-collapse: collapse;
}

.App button,
.App input,
.App optgroup,
.App select,
.App textarea {
  font-family: inherit;
  font-feature-settings: inherit;
  font-variation-settings: inherit;
  font-size: 100%;
  font-weight: inherit;
  line-height: inherit;
  letter-spacing: inherit;
  color: inherit;
  margin: 0;
  padding: 0;
}

.App button,
.App select {
  text-transform: none;
}

.App button,
.App input:where([type="button"]),
.App input:where([type="reset"]),
.App input:where([type="submit"]) {
  -webkit-appearance: button;
  background-color: transparent;
  background-image: none;
}

.App :-moz-focusring {
  outline: auto;
}

.App :-moz-ui-invalid {
  box-shadow: none;
}

.App progress {
  vertical-align: baseline;
}

.App ::-webkit-inner-spin-button,
.App ::-webkit-outer-spin-button {
  height: auto;
}

.App [type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

.App ::-webkit-search-decoration {
  -webkit-appearance: none;
}

.App ::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

.App summary {
  display: list-item;
}

.App blockquote,
.App dl,
.App dd,
.App h1,
.App h2,
.App h3,
.App h4,
.App h5,
.App h6,
.App hr,
.App figure,
.App p,
.App pre {
  margin: 0;
}

.App fieldset {
  margin: 0;
  padding: 0;
}

.App legend {
  padding: 0;
}

.App ol,
.App ul,
.App menu {
  list-style: none;
  margin: 0;
  padding: 0;
}

.App dialog {
  padding: 0;
}

.App textarea {
  resize: vertical;
}

.App input::placeholder,
.App textarea::placeholder {
  opacity: 1;
  color: theme("colors.gray.400", #9ca3af);
}

.App button,
.App [role="button"] {
  cursor: pointer;
}

.App :disabled {
  cursor: default;
}

.App img,
.App svg,
.App video,
.App canvas,
.App audio,
.App iframe,
.App embed,
.App object {
  display: block;
  vertical-align: middle;
}

.App img,
.App video {
  max-width: 100%;
  height: auto;
}

.App [hidden] {
  display: none;
}

.page {
  padding: 0;
  position: relative;
}

.content_container {
  display: flex;
  width: 100%;
  position: relative;
  height: 580px;
}

.wrapper {
  width: 100%;
}

@media (max-width: 767px) {
  .wrapper {
    padding-bottom: 4.75rem;
  }
}

@media (min-width: 768px) {
  .content_section_wrapper {
    width: 40%;
    border-right: 1px solid var(--border-lighter, #DFE1E1)
  }
}

.crossword_container {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.zoom_area_container {
  display: flex;
  align-self: center;
  overflow: hidden;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.control-container {
  display: flex;
  z-index: 9999999;
  width: 100%;
  position: absolute;
  bottom: -80px;
}

.control-container button {
  display: block;
  border: 0.12em solid var(--border-medium, #919191);
}

.table_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.crossword {
  border-collapse: collapse;
  table-layout: fixed;
  height: max-content;
  width: 28.125em;
  overflow: auto;
}

.crossword .td {
  position: relative;
  padding: 0;
  font-size: large;
  font-weight: 500;
  user-select: none;
  touch-action: none;
  box-sizing: border-box;
}

.crossword .td::before {
  display: block;
  content: "";
  padding-top: 100%;
}

.crossword .filled {
  border: 0.052em solid var(--border-medium, #919191);
}

.crossword .empty {
  visibility: hidden;
}

.hidden {
  visibility: hidden;
}

@media screen and (max-width: 480px) {
  .crossword_container {
    height: 100%;
  }

  .zoom_area_container {
    padding: 0 10px;
    height: 100%;
  }

  .table_container {
    height: 29.5rem;
  }

  .crossword {
    width: 22em;
    height: max-content
  }
}