.congratulations-container {
  background-color: white;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;


}

.congratulations-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 178px;

}

.check-mark-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #0086381a;
  height: 100px;
  width: 100px;
  margin-bottom: 10px;
  img {
    width: 24px;
  }
}

.congratulations-message {
  font-family: Open Sans;
  font-size: 24px;
  font-weight: 400;
  line-height: 28.8px;
  text-align: center;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

.bouncy {
  animation: bounce 1s;
}