.zoom_controls_container {
    display: flex;
    position: absolute;
    right: 0px;
    bottom: 0px;
    margin: 10px;
    z-index: 1;
    flex-direction: column;
    justify-content: center;
    border: 1px solid #dfe1e1;
    box-shadow: 0px 0px 30px 0px #0000001a;
    background-color: #ffffff;
    width: 50px;
    height: 180px;
    border-radius: 100px;
  }
  
  .zoom_controls_container input[type="range"] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
    writing-mode: vertical-lr;
    transform: rotate(180deg);
    height: 80px;
  }
  
  .zoom_controls_container input[type=range]::-webkit-slider-runnable-track {
    background: #919191;
    border-radius: 15px;
    width: 2px;
    box-shadow: 0px 0px 2px 0px #00000040;
  }
  
  .zoom_controls_container input[type=range]::-moz-range-track {
    background: #919191;
    border-radius: 15px;
    width: 2px;
    box-shadow: 0px 0px 2px 0px #00000040;
  }
  
  .zoom_controls_container input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    height: 16px;
    width: 16px;
    border-radius: 50%;
    border: 2px solid #919191;
    box-sizing: border-box;
    transform: translate(-45%);
    background-color: #fff;
    box-shadow: 0px 0px 4px 0px #00000040;
  }
  
  .zoom_controls_container input[type=range]::-moz-range-thumb {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    height: 16px;
    width: 16px;
    border-radius: 50%;
    border: 2px solid #919191;
    box-sizing: border-box;
    background-color: #fff;
    box-shadow: 0px 0px 4px 0px #00000040;
  }
  
  .zoom_controls_container button {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .zoom_controls_container img {
    height: 24px;
    width: 24px;
    margin: 10px;
  }