.control-banner {
  display: flex;
  position: sticky;
  flex-direction: row;
  justify-content: space-between;
  min-height: 80px;
  padding: 16px 0px;
  background: rgba(26, 140, 255, 0.25);
  box-shadow: 0px 4px 20px 0px #0000001A;
  z-index: 1;
}

.control-banner button {
  padding: 16px;
  width: 48px;
  margin: 0 4px;
}

.direction {
  font-family: Open Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-align: center;
}

.question {
  font-family: Open Sans;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  text-align: center;
}

.question img {
  display: inline;
  vertical-align: middle;
  padding-bottom: 0.2rem;
  padding-right: 0.1rem;
}
