  .letterbox {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    width: 100%;
  }

  input.cell-input {
    border-radius: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    text-align: center;
    text-anchor: middle;
    text-transform: uppercase;
    font-family: Open Sans;
    font-size: 0.55em;
    font-weight: 400;
    outline: none;
    color: #151515;
    background-color: #F6F7F8 ;
  }

  .cell-input:disabled {
    opacity: 1;
  }

  .cell-input.active {
    background-color: rgba(26, 140, 255, 0.3);
  }

  .cell-input.show-value {
    background-color: #ffbb1c;
  }

  .cell-input:read-only:not(:disabled) {
    cursor: pointer;
  }

  .cell-input:focus:not(:read-only) {
    background: rgba(21, 21, 21, 1);
    color: #FEFEFE ;
  }

  .cell-input:focus:not(:read-only)+.clue-index {
    color: #FEFEFE ;
  }

  .cell-input.fail {
    background-color: #F6F7F8 ;
  }

  .cell-input.fail + .fail {
    position: absolute;
    top: 0;
    left: 100%;
    content: "";
    width: 140%;
    height: 0.04em;
    background: #D30018;
    transform: rotate(135deg);
    transform-origin: 0 0;
  }

  .cell-input.success {
    background: rgba(0, 134, 56, 1);
    color: #FEFEFE ;
  } 

  .cell-input.success+.clue-index {
    color: #FEFEFE ;
  }

  .cell-input.show-answer {
    background: #007A9C;
    color: #FEFEFE ;
  }

  .cell-input.show-answer+.clue-index,
  .cell-input.show-answer+.fail+.clue-index,
  .cell-input.success+.clue-index {
    color: #FEFEFE ;
  }

  .clue-index {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    padding-top: 1em;
    padding-left: 0.5em;
    font-size: 0.3em;
    line-height: 0.3em;
    pointer-events: none;
  }

  .none {
    visibility: hidden;
  }
